/**  
 *   
 * const storage = require('@/utils/storage');  
 * import storage from '@/utils/storage'  
 *    
 *       
 *         string    storage.set('k', 'string');  
 *         json      storage.set('k', { "b": "3" }, 2);  
 *         array     storage.set('k', [1, 2, 3]);  
 *         boolean   storage.set('k', true);  
 *       
 *             storage.get('k')  
 *         string    storage.get('k', '')  
 *         json      storage.get('k', { "a": "1" })  
 *     /    
 *         : storage.remove('k');  
 *         storage.clear();   
 *   
 * @type {String}  
 */

const postfix = '_expiry' //    

module.exports = {

  /**  
   *    
   * @param  {[type]} k []  
   * @param  {[type]} v []  
   * @param  {[type]} t []  
   */
  set(k, v, t) {
    uni.setStorageSync(k, v)
    const seconds = parseInt(t)
    if (seconds > 0) {
      let timestamp = Date.parse(new Date())
      timestamp = timestamp / 1000 + seconds
      uni.setStorageSync(k + postfix, timestamp + '')
    } else {
      uni.removeStorageSync(k + postfix)
    }
  },

  /**  
   *    
   * @param  {[type]} k   []  
   * @param  {[type]} def []  
   */
  get(k, def) {
    const deadtime = parseInt(uni.getStorageSync(k + postfix))
    if (deadtime) {
      if (parseInt(deadtime) < Date.parse(new Date()) / 1000) {
        if (def) {
          return def
        } else {
          return false
        }
      }
    }
    const res = uni.getStorageSync(k)
    if (res) {
      return res
    }
    if (def == undefined || def == "") {
      def = false
    }
    return def
  },

  /**
   * 
   * @param {Object} k
   */
  remove(k) {
    uni.removeStorageSync(k)
    uni.removeStorageSync(k + postfix)
  },

  /**  
   *   
   * @return {[type]} [description]  
   */
  clear() {
    uni.clearStorageSync()
  }
}
