/**
 * requesthttps://ext.dcloud.net.cn/plugin?id=822
 */
import store from "@/store";
import request from "./request";
import config from "@/config";
import { isWechat } from "../app";

// api
const baseURL = config.apiUrl;
const merchantNo = config.merchantNo;

// newrequest
const $http = new request({
  // 
  baseUrl: baseURL,
  // 
  fileUrl: baseURL,
  // url
  defaultUploadUrl: "clientApi/file/upload",
  // content-type
  header: {
    "content-type": "application/json;charset=utf-8",
  },
  // , ms300000
  timeout: 300000,
  // 
  config: {
    // 
    isPrompt: true,
    // 
    load: true,
    // 
    isFactory: true,
  },
});

// 
let requestNum = 0;
// 
$http.requestStart = (options) => {
  if (options.load) {
    if (requestNum <= 0) {
      // 
      uni.showLoading({
        title: "",
        mask: true,
      });
    }
    requestNum += 1;
  }
  // 
  if (options.method == "FILE" && options.maxSize) {
    // : options.maxSize 
    const maxSize = options.maxSize;
    for (let item of options.files) {
      if (item.size > maxSize) {
        setTimeout(() => {
          uni.showToast({
            title: "",
            icon: "none",
          });
        }, 10);
        return false;
      }
    }
  }
  // 
  options.header["platform"] = store.getters.platform
    ? String(store.getters.platform)
    : "";

  // Token
  options.header["token"] = store.getters.token
    ? String(store.getters.token)
    : "";
  // 
  // options.header["merchantNo"] = uni.getStorageSync("merchantNo")
  //   ? uni.getStorageSync("merchantNo")
  //   : merchantNo;
  // // ID
  // options.header["storeId"] = uni.getStorageSync("storeId")
  //   ? uni.getStorageSync("storeId")
  //   : 0;
  // options.header["latitude"] = uni.getStorageSync("latitude")
  //   ? uni.getStorageSync("latitude")
  //   : "";
  // options.header["longitude"] = uni.getStorageSync("longitude")
  //   ? uni.getStorageSync("longitude")
  //   : "";
  // options.header["isWechat"] = isWechat() ? "Y" : "N";
  // return false 
  return options;
};

// 
$http.requestEnd = (options) => {
  // 
  if (options.load) {
    requestNum = requestNum - 1;
    if (requestNum <= 0) {
      uni.hideLoading();
    }
  }
};

// 
let loginPopupNum = 0;

// 
// 
$http.dataFactory = async (res) => {
  // console.log("", {
  //   url: res.url,
  //   resolve: res.response,
  //   header: res.header,
  //   data: res.data,
  //   method: res.method,
  // })

  if (!res.response.statusCode || res.response.statusCode != 200) {
    // (catch)
    return Promise.reject({
      statusCode: res.response.statusCode,
      // errMsg: ""
      errMsg: "http",
    });
  }

  let httpData = res.response.data;
  if (typeof httpData == "string") {
    try {
      httpData = JSON.parse(httpData);
    } catch {
      httpData = false;
    }
  }
  if (httpData === false || typeof httpData !== "object") {
    // (catch)
    return Promise.reject({
      statusCode: res.response.statusCode,
      errMsg: "",
    });
  }

  /*********()*********/
  // 
  // result.code [ 200 5000 1001 4003 ]
  // 
  if (httpData.code == 1001) {
    // 1001, 
    store.dispatch("Logout");
    // 
    if (loginPopupNum <= 0) {
      loginPopupNum++;
      uni.showModal({
        title: "",
        content: "~",
        confirmText: "",
        cancelText: "",
        success: (res) => {
          loginPopupNum--;
          if (res.confirm) {
            uni.navigateTo({
              url: "/pages/login/index",
            });
          }
        },
      });
    }
    // (catch)
    return Promise.reject({
      statusCode: 0,
      errMsg: httpData.message,
      result: httpData,
    });
  }
  // 
  else if (httpData.status == 5000) {
    if (res.isPrompt) {
      setTimeout(() => {
        uni.showToast(
          {
            title: httpData.message,
            icon: "none",
            duration: 2500,
          },
          10
        );
      });
    }
    // (catch)
    return Promise.reject({
      statusCode: 0,
      errMsg: httpData.message,
      result: httpData,
    });
  } else {
    // (then)
    return Promise.resolve(httpData);
  }

  /*********()*********/
};

// 
$http.requestError = (e) => {
  if (e.statusCode === 0) {
    throw e;
  } else {
    setTimeout(() => {
      uni.showToast({
        title: `${e.errMsg}`,
        icon: "none",
        duration: 2500,
      });
    });
  }
};
export default $http;
