import {
  mergeConfig,
  dispatchRequest,
  jsonpRequest
} from "./utils"

export default class request {

  constructor(options) {
    // 
    this.baseUrl = options.baseUrl || "";
    // 
    this.fileUrl = options.fileUrl || "";
    // 
    this.timeout = options.timeout || 6000;
    // url
    this.defaultUploadUrl = options.defaultUploadUrl || "";
    // 
    this.header = options.header || {};
    // 
    this.config = options.config || {
      isPrompt: true,
      load: true,
      isFactory: true,
      resend: 0
    };
  }

  // post
  post(url = '', data = {}, options = {}) {
    return this.request({
      method: "POST",
      data: data,
      url: url,
      ...options
    });
  }

  // get
  get(url = '', data = {}, options = {}) {
    return this.request({
      method: "GET",
      data: data,
      url: url,
      ...options
    });
  }

  // put
  put(url = '', data = {}, options = {}) {
    return this.request({
      method: "PUT",
      data: data,
      url: url,
      ...options
    });
  }

  // delete
  delete(url = '', data = {}, options = {}) {
    return this.request({
      method: "DELETE",
      data: data,
      url: url,
      ...options
    });
  }

  // jsonp(H5)
  jsonp(url = '', data = {}, options = {}) {
    return this.request({
      method: "JSONP",
      data: data,
      url: url,
      ...options
    });
  }

  // 
  async request(data) {
    // 
    let requestInfo,
      // 
      runRequestStart = false;
    try {
      if (!data.url) {
        throw {
          errMsg: "requesturl",
          statusCode: 0
        }
      }
      // 
      requestInfo = mergeConfig(this, data);
      // 
      runRequestStart = true;
      // 
      if (this.requestStart) {
        let requestStart = this.requestStart(requestInfo);
        if (typeof requestStart == "object") {
          let changekeys = ["data", "header", "isPrompt", "load", "isFactory"];
          changekeys.forEach(key => {
            requestInfo[key] = requestStart[key];
          });
        } else {
          throw {
            errMsg: "request",
            statusCode: 0,
            data: requestInfo.data,
            method: requestInfo.method,
            header: requestInfo.header,
            url: requestInfo.url,
          }
        }
      }
      let requestResult = {};
      if (requestInfo.method == "JSONP") {
        requestResult = await jsonpRequest(requestInfo);
      } else {
        requestResult = await dispatchRequest(requestInfo);
      }
      // 
      if (requestInfo.isFactory && this.dataFactory) {
        // 
        let result = await this.dataFactory({
          ...requestInfo,
          response: requestResult
        });
        return Promise.resolve(result);
      } else {
        return Promise.resolve(requestResult);
      }
    } catch (err) {
      this.requestError && this.requestError(err);
      return Promise.reject(err);
    } finally {
      // 
      if (runRequestStart) {
        this.requestEnd && this.requestEnd(requestInfo);
      }
    }
  }

}
