import { ACCESS_TOKEN, USER_ID } from "@/store/mutation-types";
import storage from "@/utils/storage";
import * as LoginApi from "@/api/login";

// 
const loginSuccess = (commit, { token, id }) => {
  // 30
  const expiryTime = 30 * 86400;
  // tokneuserId
  storage.set(USER_ID, id, expiryTime);
  storage.set(ACCESS_TOKEN, token, expiryTime);
  // store
  commit("SET_TOKEN", token);
  commit("SET_USER_ID", id);
};

const user = {
  state: {
    // token
    token: "",
    // ID
    userId: null,
  },

  mutations: {
    SET_TOKEN: (state, value) => {
      state.token = value;
    },
    SET_USER_ID: (state, value) => {
      state.userId = value;
    },
  },

  actions: {
    // 
    Register({ commit }, data) {
      return new Promise((resolve, reject) => {
        LoginApi.register(data)
          .then((response) => {
            if (response.code === 200) {
              const result = response.data;
              loginSuccess(commit, result);
            }
            resolve(response);
          })
          .catch(reject);
      });
    },

    // 
    Login({ commit }, data) {
      return new Promise((resolve, reject) => {
        LoginApi.login(data)
          .then((response) => {
            if (response.code === 200) {
              const result = response.data;
              loginSuccess(commit, result);
            }
            resolve(response);
          })
          .catch(reject);
      });
    },

    // 
    MpWxLogin({ commit }, data) {
      return new Promise((resolve, reject) => {
        LoginApi.mpWxLogin(data, { isPrompt: false })
          .then((response) => {
            const result = response.data;
            loginSuccess(commit, result);
            resolve(response);
          })
          .catch(reject);
      });
    },

    // 
    MpWxAuthLogin({ commit }, data) {
      return new Promise((resolve, reject) => {
        LoginApi.mpWxAuth(data, { isPrompt: false })
          .then((response) => {
            const result = response.data;
            loginSuccess(commit, result);
            resolve(response);
          })
          .catch(reject);
      });
    },

    // 
    Logout({ commit }, data) {
      const store = this;
      return new Promise((resolve, reject) => {
        if (store.getters.userId > 0) {
          // tokneuserId
          storage.remove(USER_ID);
          storage.remove(ACCESS_TOKEN);
          // store
          commit("SET_TOKEN", "");
          commit("SET_USER_ID", null);
          resolve();
        }
      });
    },
  },
};

export default user;
