// provide/inject
// this.$parentH5H5this.$parent.$parent.xxx
// undefined()$parentundefinedname
// (undefined)$parent
export default function $parent(name = undefined) {
    let parent = this.$parent;
    // whileH5
    while (parent) {
        // 
        if (parent.$options && parent.$options.name !== name) {
            // name
            parent = parent.$parent;
        } else {
            return parent;
        }
    }
    return false;
}