/**
 * 
 * Enum.IMAGE.name                => ""
 * Enum.getNameByKey('IMAGE')     => ""
 * Enum.getValueByKey('IMAGE')    => 10
 * Enum.getNameByValue(10)        => ""
 * Enum.getData()                 => [{key: "IMAGE", name: "", value: 10}]
 */
class Enum {
  constructor (param) {
    const keyArr = []
    const valueArr = []

    if (!Array.isArray(param)) {
      throw new Error('param is not an array!')
    }

    param.map(element => {
      if (!element.key || !element.name) {
        return
      }
      // keyA.getName(name)
      keyArr.push(element.key)
      valueArr.push(element.value)
      // keyA.B.name
      this[element.key] = element
      if (element.key !== element.value) {
        this[element.value] = element
      }
    })


    // 
    this.data = param
    this.keyArr = keyArr
    this.valueArr = valueArr

    // 
    // Object.freeze(this)
  }

  // key
  keyOf (key) {
    return this.data[this.keyArr.indexOf(key)]
  }

  // key
  valueOf (key) {
    return this.data[this.valueArr.indexOf(key)]
  }

  // keyname
  getNameByKey (key) {
    const prop = this.keyOf(key)
    if (!prop) {
      throw new Error('No enum constant' + key)
    }
    return prop.name
  }

  // valuename
  getNameByValue (value) {
    const prop = this.valueOf(value)
    if (!prop) {
      throw new Error('No enum constant' + value)
    }
    return prop.name
  }

  // keyvalue
  getValueByKey (key) {
    const prop = this.keyOf(key)
    if (!prop) {
      throw new Error('No enum constant' + key)
    }
    return prop.key
  }

  // 
  getData () {
    return this.data
  }
}

export default Enum
